.post-content-area {
  margin-top: 70px;
  background-color: #f9f9ff;
  .single-post {
    margin-bottom: 50px;
    .meta-details {
      text-align: right;
      margin-top: 35px;
      @media (max-width: 767px) {
        text-align: left;
      }
      @media (max-width: 1024px) {
        margin-top: 0px;
      }
    }

    .user-name,
    .date,
    .view,
    .comments {
      a {
        color: $txt_color;
        margin-right: 10px;
        @include transition();
        &:hover {
          color: $theme_color;
        }
      }
      .lnr {
        font-weight: 900;
        color: $black;
      }
    }
    .feature-img {
      img {
        width: 100%;
      }
    }
    .posts-title {
      h3 {
        color: #333;
        font-size: 22px;
        margin: 20px 0px;
      }
    }
    .excert {
      margin-bottom: 20px;
    }
    .primary-btn {
      background: #f1f1f1;
      color: $black !important;
      font-size: 13px;
      padding: 5px 8px;
      border: none;
      @include transition();
      border-radius: 0px !important;
      &:hover {
        background-color: $theme-color !important;
        color: $white !important;
      }
    }
  }
}

.posts-list {
  .posts-title h3 {
    @include transition();
    &:hover {
      //   color: $primary-color;
    }
  }
}

.blog-pagination {
